.banner1 {
  height: 500px;
  background: url('/img/banner1.jpg') 50% 50% repeat-y;
  overflow: hidden;
  color: $colorWhite;
  @include media-1200 {
    height: auto;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  & > .container {
    position: relative;
  }
  &__block {
    @include flex();
    position: absolute;
    flex-direction: column;
    @include media-1200 {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    &.m-block1 {
      top: 280px;
      left: 47px;
    }
    &.m-block2 {
      top: 368px;
      left: 233px;
    }
    &.m-block3 {
      top: 220px;
      left: 482px;
    }
    &.m-block4 {
      top: 389px;
      left: 785px;
    }
    &.m-block1,
    &.m-block2,
    &.m-block3,
    &.m-block4 {
      @include media-1200 {
        position: relative;
        top: 0;
        left: 0;
      }
    }
  }
  &__value {
    font-size: 42px;
    font-weight: 300;
    line-height: 1.2;
    opacity: 0;
    &.m-show {
      opacity: 1;
    }
  }
  &__label {
    font-size: 14px;
    text-align: center;
    line-height: 1.2;
  }
}

.banner2 {
  background: url('/img/banner2.jpg') 50% 50%;
  color: $colorWhite;
  @include media-960 {
    padding: 30px 0;
  }
  &__inner {
    position: relative;
    height: 460px;
    @include media-960 {
      height: auto;
    }
  }
  &__left {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    @include flex('center', 'flex-start');
    flex-direction: column;
    & > p {
      color: $colorGray500;
    }
    padding-left: 30px;
    padding-right: 80px;
    @include media-960 {
      position: relative;
      padding-left: 20px;
      padding-right: 20px;
      width: 100%;
    }
    @include media-640 {
      padding-left: 15px;
    }
  }
  &__right {
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    position: absolute;
    @include media-960 {
      display: none;
    }
  }
  &__book {
    position: absolute;
    height: 120%;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
  }
}

.banner3 {
  width: 100%;
  min-height: 350px;
  font-size: 16px;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: column;
  color: $colorWhite;
  line-height: 1.3;
  padding: 40px;
  @include media-960 {
    & > .container {
      padding: 0;
    }
  }
  @include media-768 {
    padding: 20px;
  }
  @include media-640 {
    padding: 15px;
    min-height: auto;
  }
  &.m-mt {
    margin-top: 60px;
    @include media-960 {
      margin-top: 40px;
    }
    @include media-640 {
      min-height: 200px;
      margin-top: 20px;
    }
  }
  & > * {
    position: relative;
  }
  &__bg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__sub {
    font-size: 16px;
    font-weight: 900;
  }
  &__text {
    max-width: 600px;
  }
}